.deal {
  text-align: center;
  text-decoration: none;
  padding: 10px;
  height: 100%;
  display: block;
  transition: background-color var(--transition);
  -webkit-clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
  clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
  background-color: var(--col-primary-400);
}
.deal__inner {
  transition: background-color var(--transition);
  background-color: white;
  -webkit-clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
  clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 20%);
  height: 100%;
  padding: 1rem;
  display: grid;
  grid-template-rows: auto 1fr auto;
}
.deal:hover {
  background-color: var(--col-accent-400);
  color: var(--col-primary-400);
}
.deal:hover .deal__inner {
  background-color: var(--col-accent-100);
}
.deal__disclaimer {
  font-size: var(--fs-100);
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}
.deal__logo {
  margin-bottom: 0.5rem;
  width: 127px;
  height: 80px;
}
.deal__intro {
  font-size: var(--fs-200);
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}
.deal__title {
  text-transform: uppercase;
  font-weight: var(--fw-600);
  font-size: var(--fs-300);
  padding-block: 0.5rem;
}
.deal__value {
  font-weight: var(--fw-600);
  font-size: var(--fs-500);
  color: var(--col-primary-400);
  margin-bottom: 0.5rem;
}
.deal__description_pre {
  font-size: var(--fs-200);
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}
.deal__description {
  font-size: var(--fs-300);
  font-weight: var(--fw-600);
  margin-bottom: 0.5rem;
}
.deal__finance_pre {
  font-size: var(--fs-200);
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}
.deal__finance {
  text-transform: uppercase;
  font-weight: var(--fw-600);
  font-size: var(--fs-300);
  margin-bottom: 0.5rem;
}
.deal__date {
  font-size: var(--fs-200);
}
.deal__image {
  margin-bottom: 1rem;
  position: relative;
}
.deal__image img {
  aspect-ratio: 16/9;
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}
.deal__image::before {
  content: "";
  position: absolute;
  inset: 0;
  background: url(../img/hero-overlay.svg);
  background-size: auto;
  background-position: right bottom;
  background-repeat: no-repeat;
  opacity: 0.3;
  mix-blend-mode: multiply;
}
@media (max-width: 992px) {
  .deal__sidebar {
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--col-accent-400);
  }
}
@media (min-width: 992px) {
  .deal__sidebar {
    position: -webkit-sticky;
    position: sticky;
    top: var(--h-top);
    border-right: 2px solid var(--col-accent-400);
  }
}

.related__card {
  background-color: var(--col-primary-400);
  text-decoration: none;
  display: block;
  padding: 5px;
  -webkit-clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 25%);
  clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 25%);
  transition: border-color var(--transition), background-color var(--transition);
}
.related__card:hover {
  color: var(--col-primary-400);
  background-color: var(--col-accent-400);
}
.related__inner {
  -webkit-clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 25%);
  clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 25%);
  background-color: white;
}
.related__image {
  position: relative;
}
.related__image img {
  aspect-ratio: 16/9;
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}
.related__image::before {
  content: "";
  position: absolute;
  inset: 0;
  background: url(../img/hero-overlay.svg);
