.form-control, .gform_wrapper.gravity-theme input[type=text],
.gform_wrapper.gravity-theme input[type=email],
.gform_wrapper.gravity-theme input[type=tel],
.gform_wrapper.gravity-theme input[type=number],
.gform_wrapper.gravity-theme textarea, .widget_categories select,
.widget_archive select {
  font-weight: 300;
  font-size: 0.889rem;
}

.gform_fields {
  list-style-type: none;
  margin-right: -15px;
  margin-left: -15px;
  padding: 0;
}

.gfield {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
}

.gfield textarea {
  height: 100px;
}

.validation_error,
.gfield_error .validation_message {
  color: crimson;
}

.validation_error,
.gform_confirmation_message {
  font-size: 18px;
}

.gfield_error .validation_message {
  margin-top: 5px;
}

.gfield_error select,
.gfield_error textarea,
.gfield_error input {
  border-color: crimson;
}

.gfield textarea.large {
  height: 200px !important;
}

body .gform_wrapper .gform_footer button {
  border: none;
}

.gfield_checkbox,
.gfield_radio {
  display: flex;
  justify-content: space-around;
}

.container-xl .container-xl {
  padding-left: 0;
  padding-right: 0;
}

.wp-block-columns {
  gap: 1rem;
}

@media (min-width: 992px) {
  .wp-block-group.alignfull > .wp-block-group__inner-container.wp-block-group-is-layout-constrained > .wp-block-heading:not(.has-text-align-center),
.wp-block-group.alignfull > .wp-block-group__inner-container.wp-block-group-is-layout-constrained > p:not(.has-text-align-center) {
    max-width: min(64vw, 1000px);
  }
}

blockquote.wp-block-quote {
  background-color: var(--col-off-white);
  padding-block: 10rem;
}
blockquote.wp-block-quote p {
  margin-bottom: 8rem;
  font-family: var(--ff-title);
  color: var(--col-main-blue);
  font-size: var(--fs-blockquote);
  line-height: 1.2;
}
blockquote.wp-block-quote cite {
  font-style: normal;
  font-weight: 400;
  color: var(--col-text);
  font-size: var(--fs-menu-intro);
  color: var(--col-main-blue);
}
blockquote.wp-block-quote cite::before {
  content: "";
}

.custom-list {
  list-style: none;
  padding-left: 0;
}
.custom-list li {
  position: relative;
  padding-left: 2em;
  margin-bottom: 0.5rem;
}
.custom-list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.2em;
  width: 1em;
  height: 1em;
  background-size: contain;
  background-repeat: no-repeat;
